.work-dates {
    position: absolute;
    right: 0;
    bottom: 0;
    font-size: $fontMedium;
    font-family: "AntipolExtended", sans-serif;
    font-style: italic;

    @include mobile {
        font-size: $fontSmall;
    }
}

.work-link {
    color: $black;
    text-decoration: none;

    &:last-of-type .work {
        border-bottom: none;
    }
}

.work {
    padding: $spaceExtraLarge 0;
    border-bottom: 1px solid $black;
    text-transform: uppercase;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    position: relative;

    &:hover figure {
        transform: scale(1.05);
    }

    .description, a {
        font-size: $fontSmall;
        display: block;
        font-weight: 200;
    }    

    .title {
        font-size: $fontMedium;
        font-family: "MonumentExtended", sans-serif;
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        z-index: 10;

        @include mobile {
            font-size: $fontBase;
            position: static;
            transform: none;
            line-height: 1.1;
            margin-top: $spaceBase;
        }
    }

    .info {
        display: flex;
        justify-content: space-between;
        font-size: $fontSmall;

        @include mobile {
            display: block;
        }

        span {
            display: block;
        }

        .hidden-mb {
            @include mobile {
                display: none;
            }
        } 

        em {
            font-family: "AntipolExtended", sans-serif;
        }
    }

    figure {
        width: 800px;
        height: 470px;
        background: #A3C8FF;
        background-size: cover;
        background-position: center;
        align-self: center;
        margin: $spaceLarge 0;
        transition: all .3s cubic-bezier(0.165, 0.84, 0.44, 1);

        @include mobile {
            width: 100%;
            height: 300px;
            margin: $spaceMedium 0;
        }
    }
}

.project-detail {
    flex-direction: column;
    text-align: left;
    font-size: $fontSmall;
    margin-top: $spaceExtraLarge;

    li {
        display: flex;
        margin-bottom: $spaceSmall;

        @include mobile {
            flex-direction: column;
            margin-bottom: $spaceBase;
        }
    }

    .detail-name {
        width: 200px;
    }

    .detail-value {
        flex: 1;
    }
}

.horizontal-slider {
    margin: $spaceExtraLarge 0;
    display: flex;
    overflow-x: scroll;
    scroll-snap-type: x mandatory;
    padding: 0 40vw;

    @include mobile {
        padding: 0 0 0 $spaceMedium;
    }

    .slide {
        margin: 0 $spaceExtraLarge 0 0;
        scroll-snap-align: center;
        max-width: 1160px;
        height: 660px;

        @include mobile {
            height: auto;
            width: 90%;
            flex-shrink: 0;
            margin-right: $spaceBase;
        }

        img {
            max-height: 100%;

            @include mobile {
                max-width: 100%;
            }
        }
    }
}

.portfolio-section {
    margin: $spaceExtraLarge 0;

    h3 {
        font-size: $fontBase;
        font-weight: 400;
        margin: 0 0 $spaceBase 0;
    }

    h4 {
        margin: 0 0 $spaceSmall 0;
        font-size: $fontSmall + 6;
        font-weight: 400;
    }

    p {
        margin: $spaceSmall 0 $spaceMedium 0;
        font-size: $fontSmall;
    }
}

.big-image {
    margin: 0;
    padding: 0;

    img {
        max-width: 100%;
    }
}

.cols {
    display: flex;
    
    > * {
        flex: 1;
    }

    figure {
        padding: 0;
        margin: 0;

        img {
            max-width: 100%;
            max-height: 100%;
        }
    }

    @include mobile {
        flex-direction: column;
        
        > * {
           margin-left: 0 !important;
           margin-right: 0 !important;
        }
    }
}

.two-cols {
    > *:nth-child(1) {
        margin-right: $spaceMedium;
    }
    
    > *:nth-child(2) {
        margin-left: $spaceMedium;
    }

    ul {
        flex-direction: column;
        list-style: disc;
        padding-left: $spaceMedium;
        font-size: $fontSmall;
    }
}

.three-cols {
    > *:nth-child(1) {
        margin-right: $spaceBase;
    }
    
    > *:nth-child(2) {
        margin: 0 $spaceSmall; 
    }
    
    > *:nth-child(3) {
        margin-left: $spaceBase;
    }
}

.four-cols {
    > *:nth-child(1) {
        margin-right: $spaceBase;
    }
    
    > *:nth-child(2), > *:nth-child(3) {
        margin: 0 $spaceSmall; 
    }
    
    > *:nth-child(4) {
        margin-left: $spaceBase;
    }
}

.project-footer {
    margin-top: $spaceExtraLarge*2;

    p {
        font-family: "MonumentExtended", sans-serif;
        line-height: 1.4;
    }
}